
.gallery{
	padding-top: 120px;
	padding-bottom: 120px;
}
.gallery__container{
	width: 1170px;
	margin: 0 auto;
}
.foto__block{
	margin-right: -28px;
}
.foto__item{
	position: relative;
	width: 270px;
	height: 200px;
	margin: 0;
	display: inline-block;
	position: relative;
	padding-right: 26px;
    padding-bottom: 26px;
}
.foto__item:hover .foto__text{
	opacity: 1;
}
.foto__text{
	display: inline-block;
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	background-color: #ffeb33;
	width: 270px;
	height: 200px;
}
.foto__text__title{
	position: absolute;
	top: 75px;
	left: 85px;
	font-size: 19px;
	font-family: 'open_sansregular', sans-serif;
}
.foto__text__text{
	position: absolute;
	top: 107px;
	left: 50px;
	font-size: 15px;
	color: #fff;
}
.foto__img{
	display: inline-block;
	position: relative;
	/*padding-left: 26px;*/
	padding-right: 26px;
    padding-bottom: 26px;
    width: 270px;
    height: 200px;
}
.gallery__subtitle__bottom{
	color: #ffeb33;
	font-size: 12px;
	padding-top: 20px;
	text-align: center;
	font-family: 'open_sansregular', sans-serif;
}
